.search-bar {
    #keyword {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #F6F6F6;
        border: 1px solid #CECECE;
        border-radius: 6px;
        margin: 10px;
        padding-left: 8px;

        input[type="search"] {
            border: none;
            width: 100%;
            background-color: unset;
            height: 90%;
            padding: 10px;

            font-family: 'Lato', sans-serif;


            &:focus {
                outline: none;
            }
        }
    }
}